@import 'var';

$prime: #FF6E48;
$second: #0C1016;

body, html {


    font-family: 'Roboto';
    width: 100%;
    height: 100%;
    background-color: $second;
    font-size: 10px; }

* {
    box-sizing: border-box; }




.glitch {
    color: white;
    font-size: 10rem;
    width: 60rem;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin: 2%;

    &::after {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: 0.5rem;
        color: white;
        background-color: $second;
        overflow: hidden;
        text-shadow: -0.5rem 0 $prime;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 2s infinite linear alternate-reverse;
        margin: 2%;
        left: 30%; }

    &::before {
        content: attr(data-trick);
        position: absolute;
        top: 0;
        left: -1rem;
        color: white;
        background-color: $second;
        overflow: hidden;
        text-shadow: 0.3rem 0 $prime;
        clip: rect(0, 90rem, 0, 0);
        animation: noise 4s infinite linear alternate-reverse;
        margin: 2%;
        left: 30%; } }

@keyframes noise {
    $steps: 25;
    @for $i from 0 through $steps {
        #{ percentage($i * (1/$steps))} {
            clip: rect(random(12)+rem, 99.99rem, random(12)+rem, 0); } } }

.flexbox {
    width: 90%;
    margin: 5%;
    background: #dadada; }


.flexelement {
    background: #ff6600;
    color: #ffffff;
    text-align: center;
    margin: 10px;
    padding: 15px 0 15px 0; }

svg {
	position: fixed;
	top: 0;
	left: 0;
	height: 100%;
	width: 30rem; }

#sidebarinnline {
	transition: all 250ms;
	cursor: pointer; }


.logo {
	position: absolute;
	top: 2rem;
	left: 2.5rem;
	z-index: 3;
	width: 5rem;
	height: 5rem; }


aside {
	transition: all 250ms;
	position: absolute;
	top: 0;
	left: -20rem;
	bottom: 0;
	z-index: 2;
	width: 30rem;
	cursor: pointer; }


aside .cta {
	position: absolute;
	top: 50%;
	right: 3rem;
	transform: rotate(-90deg);
	font-size: 2rem;
	color: #ffffff;
	font-family: sans-serif;
	font-size: 1.6rem;
	transition: all 350ms; }


aside.open-sidebar {
	left: 0; }


aside.open-sidebar nav {
	opacity: 1; }


aside.open-sidebar .cta {
	opacity: 0; }


aside nav {
	margin: 20rem 0 0 0;
	padding: 0;
	width: 100%;
	opacity: 1; }


aside nav ul {
	margin: 0;
	padding: 0;
	width: 100%; }


aside nav ul li {
	margin: 0;
	padding: 0.5rem 0 0.5rem 0;
	list-style: none;
	width: 100%;
	text-align: center; }


aside nav ul li a {
	transition: all 350ms;
	text-decoration: none;
	color: #ffffff;
	font-family: sans-serif;
	font-size: 1.6rem; }


aside nav ul li a:hover {
	color: #1b2431; }




